<template>
    <yd-layout>
        <div class="departPage">
            <div class="binding" v-if="Object.keys(phoneList).length==0" @click="$router.push('/bindingAccount')">
                <div class="bindingText1">云闪付APP账户未绑定</div>
                <router-link to="/bindingAccount">
                    <div class="bindingText2">去绑定</div>
                </router-link>
            </div>
            <div class="binding" v-if="Object.keys(phoneList).length>0" @click="$router.push('/bindingAccount')">
                <div class="bindingText1">云闪付APP账户已绑定</div>
                <router-link to="/bindingAccount">
                    <div class="bindingText2">去绑定</div>
                </router-link>
            </div>
            <div class="proDate">
                <div class="dataText" @click="quarterPopup=true" v-text="quarter"></div>
            </div>
            <div class="retailedRules">
                <div class="rr_left" @click="$router.push('activityDetails')">活动细则</div>
                <div class="rr_right">数据更新于{{updateTime}}</div>
            </div>
            <div class="sortSwiper">
                <div class="swiper-container">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide"><span class="slideOne"><span class="slideText" :class="{'slideActive':slideNum==1}" @click="slideNum=1;oneDeal=underlineArr">线下交易</span></span>
                        </div>
                        <div class="swiper-slide"><span class="slideOne"><span class="slideText" :class="{'slideActive':slideNum==2}" @click="slideNum=2;oneDeal=onlineArr">线上交易</span></span>
                        </div>
                        <div class="swiper-slide"><span class="slideOne"><span class="slideText" :class="{'slideActive':slideNum==3}" @click="slideNum=3;oneDeal=appArr">APP内支付</span></span>
                        </div>
                        <div class="swiper-slide"><span class="slideOne"><span class="slideText" :class="{'slideActive':slideNum==4}" @click="slideNum=4;oneDeal=shopArr">季度商户数</span></span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="slideDetial" v-show="queryFlag==false">
                <div class="detailOne" v-for="(item, index) in dealNum" :key="index" v-show="item.org.indexOf(deptName)!=-1">
                    <yd-flexbox>
                        <yd-flexbox-item v-text="item.name"></yd-flexbox-item>
                        <div class="noDab" v-if="(slideNum!=2&&item.jobClass=='1'&&parseInt(showPerson(slideNum,item))<5)||(slideNum!=2&&item.jobClass=='2'&&parseInt(showPerson(slideNum,item))<3)">未达标</div>
                        <div class="hasDab" v-if="(slideNum!=2&&item.jobClass=='1'&&parseInt(showPerson(slideNum,item))>=5)||(slideNum!=2&&item.jobClass=='2'&&parseInt(showPerson(slideNum,item))>=3)">已达标</div>
                        <div class="noDab" v-if="(slideNum==2&&item.jobClass=='1'&&parseInt(showPerson(slideNum,item))<3)||(slideNum==2&&item.jobClass=='2'&&parseInt(showPerson(slideNum,item))<1)">未达标</div>
                        <div class="hasDab" v-if="(slideNum==2&&item.jobClass=='1'&&parseInt(showPerson(slideNum,item))>=3)||(slideNum==2&&item.jobClass=='2'&&parseInt(showPerson(slideNum,item))>=1)">已达标</div>
                    </yd-flexbox>
                    <yd-flexbox>
                        <yd-flexbox-item class="departName" v-text="getOrg(item.org)"></yd-flexbox-item>
                        <div class="noDab" v-if="(slideNum!=2&&item.jobClass=='1'&&parseInt(showPerson(slideNum,item))<5)||(slideNum!=2&&item.jobClass=='2'&&parseInt(showPerson(slideNum,item))<3)"><span class="noDabNum" v-text="showPerson(slideNum,item)"></span>笔</div>
                        <div class="hasDab" v-if="(slideNum!=2&&item.jobClass=='1'&&parseInt(showPerson(slideNum,item))>=5)||(slideNum!=2&&item.jobClass=='2'&&parseInt(showPerson(slideNum,item))>=3)"><span class="noDabNum" v-text="showPerson(slideNum,item)"></span>笔</div>
                        <div class="noDab" v-if="(slideNum==2&&item.jobClass=='1'&&parseInt(showPerson(slideNum,item))<3)||(slideNum==2&&item.jobClass=='2'&&parseInt(showPerson(slideNum,item))<1)"><span class="noDabNum" v-text="showPerson(slideNum,item)"></span>笔</div>
                        <div class="hasDab" v-if="(slideNum==2&&item.jobClass=='1'&&parseInt(showPerson(slideNum,item))>=3)||(slideNum==2&&item.jobClass=='2'&&parseInt(showPerson(slideNum,item))>=1)"><span class="noDabNum" v-text="showPerson(slideNum,item)"></span>笔</div>
                    </yd-flexbox>
                </div>
            </div>
            <div class="queryDepart" v-show="queryFlag==true">
                <ul>
                    <li :class="{'li_active':liNum==-1}" @click="liNum=-1;deptName=''"><span>全部</span></li>
                    <li :class="{'li_active':liNum==index}" @click="liNum=index;deptName=item.orgname" v-for="(item, index) in departList" :key="index"><span v-text="item.orgname"></span></li>
                </ul>
            </div>
        </div>
        <yd-popup v-model="quarterPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="quarterPopup = false">取消</div>
                <div class="rightBtn" v-on:click="quarterPopup = false;quarter=quarterNum;deptTotle(statusRadio)">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" :class="{active:statusRadio==1}" @click="statusRadio=1;quarterNum=year+'年第一季度'" v-show="monthNum>=1">
                        {{year}}年第一季度
                    </li>
                    <li class="li" :class="{active:statusRadio==2}" @click="statusRadio=2;quarterNum=year+'年第二季度'" v-show="monthNum>=2">
                        {{year}}年第二季度
                    </li>
                    <li class="li" :class="{active:statusRadio==3}" @click="statusRadio=3;quarterNum=year+'年第三季度'" v-show="monthNum>=3">
                        {{year}}年第三季度
                    </li>
                    <li class="li" :class="{active:statusRadio==4}" @click="statusRadio=4;quarterNum=year+'年第四季度'" v-show="monthNum>=4">
                        {{year}}年第四季度
                    </li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar class="proPage" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999" v-show="quarterPopup==false">
            <yd-tabbar-item link="/productPage" title="我的产品体验">
                <yd-icon name="myPro" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item link="" title="部门产品体验" active>
                <yd-icon name="depPro" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <div class="queryImg">
                <img src="../../../static/images/handle_sai.png" class="img" v-show="queryFlag==false" @click="queryFlag=true">
                <img src="../../../static/images/handle_sai1.png" class="img" v-show="queryFlag==true" @click="queryFlag=false">
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            slideNum: 1,
            queryFlag: false,
            liNum: -1,
            quarterPopup: false,
            year: '',
            statusRadio: -1,
            monthNum: 0,
            quarter: '',
            departList: {},
            dealNum: [],
            oneDeal: [],
            appArr: [],
            onlineArr: [],
            shopArr: [],
            deptName: '',
            underlineArr: [],
            quarterNum: '',
            phoneList: {},
            updateTime: ''
        }
    },
    mounted() {
        let vm = this;
        vm.loadSwiper();
        let date = new Date();
        vm.year = vm.formatDate(date, 'yyyy');
        vm.month = parseInt(vm.formatDate(date, 'MM'));
        vm.monthNum = Math.ceil(vm.month / 3);
        vm.statusRadio = vm.monthNum;
        if (vm.monthNum == 1) {
            vm.quarter = vm.year + '年第一季度';
        } else if (vm.monthNum == 2) {
            vm.quarter = vm.year + '年第二季度';
        } else if (vm.monthNum == 3) {
            vm.quarter = vm.year + '年第三季度';
        } else if (vm.monthNum == 4) {
            vm.quarter = vm.year + '年第四季度';
        }
        vm.deptTotle(vm.monthNum);
        vm.getDeptAllOrg();
        vm.app_phone();
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        // swiper插件
        loadSwiper() {
            let vm = this;
            vm.$nextTick(() => {
                this.galleryTop = new Swiper('.swiper-container', {
                    initialSlide: vm.departNum,
                    slidesPerView: 'auto',
                    paginationClickable: true,
                    observer: true, // 修改swiper自己或子元素时，自动初始化swiper
                    observeParents: true // 修改swiper的父元素时，自动初始化swiper
                });
            });
        },
        deptTotle(index) {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/empproexperience/dept', {
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'season': index,
                'year': vm.year,
                'leader': vm.getUserInfo.role
            }, function(res) {
                vm.dealNum = res.jsonData.list;
                vm.updateTime = res.jsonData.updateTime;
                // vm.appArr = res.jsonData.app;
                // vm.onlineArr = res.jsonData.online;
                // vm.underlineArr = res.jsonData.underline;
                // vm.shopArr = res.jsonData.shop;
                // vm.oneDeal = res.jsonData.underline;
            });
        },
        // 查询科室
        getDeptAllOrg() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/getDeptAllOrg', {
                'name': vm.getUserInfo.ssotoken
            }, function(res) {
                vm.departList = res.jsonData;
            });
        },
        getOrg(item) {
            let arr = item.split('/');
            return arr[1];
        },
        // filterData(org) {
        //     let vm = this;
        //     vm.underlineArr = [];
        //     vm.appArr = [];
        //     vm.shopArr = [];
        //     vm.onlineArr = [];
        //     for (let i = 0; i < vm.dealNum.underline.length; i++) {
        //         if (vm.dealNum.underline[i].org.indexOf(org) != -1) {
        //             console.log(vm.dealNum.underline[i]);
        //             vm.underlineArr.push(vm.dealNum.underline[i]);
        //         }
        //     }
        //     vm.oneDeal = vm.underlineArr;
        //     for (let i = 0; i < vm.dealNum.app.length; i++) {
        //         if (vm.dealNum.app[i].org.indexOf(org) != -1) {
        //             vm.appArr.push(vm.dealNum.app[i]);
        //         }
        //     }
        //     for (let i = 0; i < vm.dealNum.shop.length; i++) {
        //         if (vm.dealNum.shop[i].org.indexOf(org) != -1) {
        //             vm.shopArr.push(vm.dealNum.shop[i]);
        //         }
        //     }
        //     for (let i = 0; i < vm.dealNum.online.length; i++) {
        //         if (vm.dealNum.online[i].org.indexOf(org) != -1) {
        //             vm.onlineArr.push(vm.dealNum.online[i]);
        //         }
        //     }
        // },
        showPerson(num, item) {
            if (num == 1) {
                return item.underline;
            } else if (num == 2) {
                return item.online;
            } else if (num == 3) {
                return item.app;
            } else if (num == 4) {
                return item.mcc;
            }
        },
        // 账号列表
        app_phone() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/empproexperience/app_phone', {
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                vm.phoneList = res.jsonData;
            });
        }
    }
}
</script>
<style lang="less" scoped>
@import './experience.less';
.departPage {
    background-color: #fff;
    height: 100%;
}

.yd-icon-myPro:before {
    display: block;
    content: '';
    width: .3rem;
    height: .4rem;
    background: url(../../../static/images/experience/myPro2@3x.png) no-repeat 0 0;
    background-size: .3rem;
}

.yd-icon-depPro:before {
    display: block;
    content: '';
    width: .4rem;
    height: .4rem;
    background: url(../../../static/images/experience/depPro2@3x.png) no-repeat 0 0;
    background-size: .4rem;
}

.yd-flexbox-item {
    height: .65rem;
    line-height: .65rem;
    font-size: .32rem;
    color: #333;
}

.hasDab {
   font-size: .24rem; 
   color: #333;
}
</style>


